
window.Bundle = window.Bundle || {};
const reportBundleErrors = (...errors: unknown[]) => {
    console.warn("Overlay Script load errors: ", errors);
};
window.Bundle.initOverlayThing = () => {
    import(/* webpackChunkName: "OverlayThing.main" */ "./index")
        .then(() => {
            import(/* webpackChunkName: "libf" */ "fancyutils")
                .then(({ loadCss }) => {
                    window.Bundle.loaded = true;
                    loadCss("MoreShare");
                })
                .catch(reportBundleErrors);
        })
        .catch(reportBundleErrors);
    delete window.Bundle.initOverlayThing;
};

window.Bundle.initOverlayArticle = () => {
    import(/* webpackChunkName: "OverlayArticle.main" */ "../overlay-article/index")
        .then(() => {
            import(/* webpackChunkName: "libf" */ "fancyutils")
                .then(({ loadCss }) => {
                    loadCss("MoreShare");
                })
                .catch(reportBundleErrors);
        })
        .catch(reportBundleErrors);
    delete window.Bundle.initOverlayArticle;
};

// static pages
if (window.Bundle.shouldInitOverlayArticle) {
    window.Bundle.initOverlayArticle();
    window.Bundle.shouldInitOverlayArticle = false;
} else {
    window.Bundle.initOverlayThing();
}

function onLoad() {
    const thingRegex = /\/(sales|things)\/(\d+)/;
    const articleRegex = /\/articles\/(.+)/;
    const initialHref = location.href;

    $(window).on("popstate.overlay", function(event) {
        if (
            location.href !== initialHref &&
            !(location.pathname.match(thingRegex) || location.pathname.match(articleRegex))
        ) {
            event.preventDefault();
            location.reload();
        }
    });
}

if (document.readyState !== "loading") {
    onLoad();
} else {
    document.addEventListener("DOMContentLoaded", onLoad);
}
